* {
  box-sizing: border-box;
}

html {}

body {
  margin: 0;
  color: var(--gray1);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: .5em 0 .5em;
  margin: 0;
}

p {
  margin: 0 0 0;
}

a {
  text-decoration: none;
  color: unset;
}



/* Dont stretch my Media */
img,
embed,
video,
object {
  max-width: 100%
}

/* Simple and useful CSS Debugging Technique */
.debug * {
  outline: 1px solid gold
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}